import React, { useEffect, useState } from 'react';
import './App.scss';
import classNames from 'classnames';

import usersFromServer from './api/users';
import categoriesFromServer from './api/categories';
import productsFromServer from './api/products';
import {
  CategoriesFilter,
  ProductList,
  SearchBar,
  UserFilter,
} from './components';

const products = productsFromServer.map((product) => {
  const category = categoriesFromServer.find(c => c.id === product.categoryId);
  const user = usersFromServer.find(u => u.id === category?.ownerId);

  return { ...product, category, user };
});

export const App = () => {
  const [visibleProducts, setVisibleProducts] = useState(products);
  const [query, setQuery] = useState('');
  const [user, setUser] = useState('all');
  const [categories, setCategories] = useState([]);
  // eslint-disable-next-line quote-props
  const [sortType, setSortType] = useState({ 'id': 'asc' });
  // const [order, setOrder] = useState('asc');
  const filteredByQuery = data => (
    data.filter(product => (
      product.name.toLowerCase().includes(query.toLowerCase())
    ))
  );

  const filteredByName = (data) => {
    if (user === 'all') {
      return data;
    }

    return data.filter(product => product.user.name === user);
  };

  const filteredByCategories = (data) => {
    if (!categories.length) {
      return data;
    }

    return data.filter(product => categories.includes(product.category.title));
  };

  const resetFilters = () => {
    setQuery('');
    setUser('all');
    setCategories([]);
  };

  const handleSort = type => () => {
    if (sortType[type] !== 'asc') {
      setSortType({ [type]: 'asc' });
    } else {
      setSortType({ [type]: 'desc' });
    }
  };

  const sortProducts = (data) => {
    const sortedProducts = [...data];

    const sortByOrder = (a, b, order, callback) => {
      const result = callback(a, b);

      return order === 'asc' ? result : result * -1;
    };

    const type = Object.keys(sortType)[0];

    switch (type) {
      case 'id':
        sortedProducts
          .sort((a, b) => sortByOrder(a, b, sortType[type], (x, y) => (
            +x[type] - +y[type]
          )));
        break;
      case 'name':
        sortedProducts
          .sort((a, b) => sortByOrder(a, b, sortType[type], (x, y) => (
            x[type].localeCompare(y[type])
          )));
        break;
      case 'title':
        sortedProducts
          .sort((a, b) => sortByOrder(a, b, sortType[type], (x, y) => (
            x.category[type].localeCompare(y.category[type])
          )));
        break;
      case 'userName':
        sortedProducts
          .sort((a, b) => sortByOrder(a, b, sortType[type], (x, y) => (
            x.user.name.localeCompare(y.user.name)
          )));
        break;
      default:
        break;
    }

    return sortedProducts;
  };

  useEffect(() => {
    let result = [...products];

    result = filteredByQuery(result);
    result = filteredByName(result);
    result = filteredByCategories(result);
    result = sortProducts(result);
    setVisibleProducts(result);
  }, [query, user, categories, sortType]);

  return (
    <div className="section">
      <div className="container">
        <h1 className="title">Product Categories</h1>

        <div className="block">
          <nav className="panel">
            <p className="panel-heading">Filters</p>

            <UserFilter
              users={usersFromServer}
              activeUser={user}
              onChangeCallback={setUser}
            />

            <SearchBar onChangeCallback={setQuery} query={query} />

            <CategoriesFilter
              categories={categoriesFromServer}
              selectedCategories={categories}
              onChangeCallback={setCategories}
            />

            <div className="panel-block">
              <a
                data-cy="ResetAllButton"
                href="#/"
                className="button is-link is-outlined is-fullwidth"
                onClick={resetFilters}
              >
                Reset all filters
              </a>
            </div>
          </nav>
        </div>

        <div className="box table-container">
          <table
            data-cy="ProductTable"
            className="table is-striped is-narrow is-fullwidth"
          >
            {!visibleProducts.length
              ? (
                <tbody>
                  <tr>
                    <td>
                      <p data-cy="NoMatchingMessage">
                        No products matching selected criteria
                      </p>
                    </td>
                  </tr>
                </tbody>
              )
              : (
                <>
                  <thead>
                    <tr>
                      <th>
                        <span className="is-flex is-flex-wrap-nowrap">
                          ID

                          <a
                            href="#/"
                            onClick={handleSort('id')}
                          >
                            <span className="icon">
                              <i
                                data-cy="SortIcon"
                                className={classNames({
                                  fas: true,
                                  'fa-sort-up': sortType.id === 'asc',
                                  'fa-sort-down': sortType.id === 'desc',
                                  'fa-sort': !sortType.id,
                                })}
                              />
                            </span>
                          </a>
                        </span>
                      </th>

                      <th>
                        <span className="is-flex is-flex-wrap-nowrap">
                          Product

                          <a
                            href="#/"
                            onClick={handleSort('name')}
                          >
                            <span className="icon">
                              <i
                                data-cy="SortIcon"
                                className={classNames({
                                  fas: true,
                                  'fa-sort-up': sortType.name === 'asc',
                                  'fa-sort-down': sortType.name === 'desc',
                                  'fa-sort': !sortType.name,
                                })}
                              />
                            </span>
                          </a>
                        </span>
                      </th>

                      <th>
                        <span className="is-flex is-flex-wrap-nowrap">
                          Category

                          <a
                            href="#/"
                            onClick={handleSort('title')}
                          >
                            <span className="icon">
                              <i
                                data-cy="SortIcon"
                                className={classNames({
                                  fas: true,
                                  'fa-sort-up': sortType.title === 'asc',
                                  'fa-sort-down': sortType.title === 'desc',
                                  'fa-sort': !sortType.title,
                                })}
                              />
                            </span>
                          </a>
                        </span>
                      </th>

                      <th>
                        <span className="is-flex is-flex-wrap-nowrap">
                          User

                          <a
                            href="#/"
                            onClick={handleSort('userName')}
                          >
                            <span className="icon">
                              <i
                                data-cy="SortIcon"
                                className={classNames({
                                  fas: true,
                                  'fa-sort-up': sortType.userName === 'asc',
                                  'fa-sort-down': sortType.userName === 'desc',
                                  'fa-sort': !sortType.userName,
                                })}
                              />
                            </span>
                          </a>
                        </span>
                      </th>
                    </tr>
                  </thead>
                  <tbody>
                    <ProductList products={visibleProducts} />
                  </tbody>
                </>
              )
            }
          </table>
        </div>
      </div>
    </div>
  );
};
